.subscriptions {

  &__container {
    background-color: $bg;
    padding: 48px 24px 36px;
    border-radius: 16px;

    @media (max-width: 669px) {
      padding: 48px 16px 36px;
    }

  }

  &__title {
    margin: 0 auto;
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -0.01em;
    color: $black;

    @media (max-width: 669px) {
      margin-bottom: 15px;
      text-align: left;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -12px;

    @media (max-width: 1024px) {
      justify-content: center;
    }
  }
}


.subscriptions-form {
  max-width: 928px;
  margin: 0 auto 20px;

  &__label {
    display: flex;
    position: relative;
    margin-bottom: 18px;
  }

  &__input {
    width: 100%;
    height: 56px;
    font-size: 16px;
    line-height: 1.5;
    background-color: $white;
    border-radius: 30px 100px 100px 30px;
    border: none;
    padding: 16px 173px 16px 16px;

    &::placeholder {
      font-size: 16px;
      line-height: 1.5;
      color: $text;
    }
  }

  &__btn {
    padding: 16px 32px;
    background-color: $accent;
    border-radius: 100px;
    position: absolute;
    right: 0;
    transition: background-color 0.3s ease-in-out;

    &:hover {
      background-color: $cornflower;
    }

    @media (max-width: 575px) {
      padding: 20px 28px;
      border-radius: 100%;

      &::before {
        content: '';
        position: absolute;
        width: 9px;
        height: 14px;
        top: 21px;
        right: 24px;
        background-image: url(../img/next.webp);
      }

    }
  }

  &__btn-text {
    font-size: 16px;
    line-height: 1.5;
    color: $white;

    @media (max-width: 575px) {
      font-size: 0;
    }
  }

  &__switch {
    position: relative;
    padding-left: 40px;

    &::before {
      content: '';
      position: absolute;
      width: 32px;
      height: 16px;
      left: 0;
      top: 0;
      border-radius: 20px;
      background-color: $alto;
      transition-duration: 0.3s;
    }

    &::after {
      content: '';
      position: absolute;
      width: 14px;
      height: 14px;
      top: 1px;
      right: 224px;
      border-radius: 100%;
      background-color: $white;
      z-index: 1;
      transition-duration: 0.3s;
    }
  }

  &__switch-input {
    display: none;

    &:checked+.subscriptions-form__switch::before {
      background-color: $accent;
    }

    &:checked+.subscriptions-form__switch::after {
      right: 208px;
    }
  }
}

.subscriptions-card {
  padding: 24px;
  padding-bottom: 26px;
  background-color: $white;
  border-radius: 16px;
  max-width: 476px;
  width: 100%;
  transition: box-shadow 0.3s ease-in-out;
  margin: 12px;

  &:hover {
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.08);
  }

  @media (max-width: 575px) {
    max-width: 343px;
  }

  &__link {
    display: flex;
    justify-content: space-between;

    @media (max-width: 575px) {
      position: relative;
    }
  }

  &__content {
    max-width: 324px;
  }

  &__suptitle {
    font-size: 13px;
    line-height: 1.2;
    color: $gray;
  }

  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    color: $black;
    margin-bottom: 11px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.4;
    color: $text;
    margin-bottom: 16px;
  }

  &__descriptions {
    margin-bottom: 18px;
  }

  &__descr {
    font-size: 14px;
    line-height: 1.4;
    padding-left: 24px;
    position: relative;
    margin-bottom: 10px;


    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 1px;
      width: 16px;
      height: 16px;
      background-image: url(../img/vector.webp);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }

  &__switch {
    position: relative;
    padding-left: 56px;

    &::before {
      content: '';
      position: absolute;
      width: 48px;
      height: 24px;
      left: 0;
      top: -4px;
      border-radius: 20px;
      background-color: $bg;
      transition-duration: 0.3s;
      cursor: pointer;
    }

    &::after {
      content: '';
      position: absolute;
      width: 22px;
      height: 22px;
      top: -3px;
      right: 227px;
      border-radius: 100%;
      background-color: $white;
      z-index: 1;
      transition-duration: 0.3s;
      cursor: pointer;
    }
  }

  &__switch-input {
    display: none;

    &:checked+.subscriptions-card__switch::before {
      background-color: $accent;
    }

    &:checked+.subscriptions-card__switch::after {
      right: 203px;
    }

  }

  &__images {
    overflow: hidden;
    border-radius: 8px;
  }

  &__img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    object-position: center;

    @media (max-width: 575px) {
      position: absolute;
      top: 0px;
      right: 0px;
      width: 40px;
      height: 40px;
    }
  }
}
