.top {
  &__inner {
    text-align: center;
  }

  &__title {
    font-weight: 500;
    font-size: 56px;
    letter-spacing: -0.01em;
    color: $black;
    margin-bottom: 26px;

    @media (max-width: 669px) {
      font-size: 32px;
    }
  }

  &__list {
    display: flex;
    justify-content: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 25px;
    }
  }

  &__link {
    display: inline-block;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.3;
    color: $text;
    padding-bottom: 17px;
    border-bottom: 2px solid transparent;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;


    &:hover {
      color: $accent;
      border-color: $accent;
    }

    &--active {
      color: $accent;
      border-color: $accent;
    }

    &:active {
      color: $cornflower;
      border-color: $cornflower;
    }
  }

}
