.breadcrumbs {
  display: flex;
  justify-content: center;
  margin-bottom: 24px;

  &__list {
    display: flex;
  }

  &__item {
    position: relative;

    &:last-child {
      .breadcrumbs__link {
        color: $accent;
        border-color: $accent;
      }
    }

    &:not(:last-child) {
      margin-right: 10px;
      padding-right: 20px;

      &::after {
        content: '';
        position: absolute;
        background-image: url(../img/arrow.svg);
        width: 10px;
        height: 4px;
        right: 0;
        top: 7px;
      }
    }
  }

  &__link {
    padding-bottom: 1px;
    font-size: 13px;
    line-height: 1.3;
    color: $gray;
    border-bottom: 1px solid #e6edff;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &:hover {
      color: $accent;
      border-color: $accent;
    }

    &:active {
      color: $cornflower;
      border-color: $cornflower;
    }
  }
}
